@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:color";

.contenedor-boletos {
  background-color: v.$verde;
  h3 {
    color: v.$blanco;
  }
}

.boletos {
  @include m.contenedor;
  @include m.tablet {
    @include m.grid(2);
  }
}

.pase {
  border-radius: 0.8rem;
  background-color: color.adjust(v.$verde, $lightness: 1.5%);
  transition: transform 0.3s ease-in-out;

  &:hover {
    transform: scale(1.1);
  }
  &-nombre {
    font-size: 3.6rem;
    font-weight: 900;
    text-align: center;
    color: v.$blanco;
  }
  &-header,
  &-footer {
    padding: 0.5rem;

    p {
      text-align: center;
      color: v.$blanco;
      margin: 0;
    }
  }
  &-header {
    background-color: v.$rosa;

    p {
      text-transform: uppercase;
      font-size: 2.2rem;
      margin: 0;
    }
  }

  &-body {
    background-color: v.$blanco;
    ul {
      @include m.reset-list;

      li {
        padding: 2rem;
        border-bottom: 1px solid v.$gris;
      }
    }
  }

  &-footer {
    background-color: v.$blanco;
    padding: 3rem;

    p {
      font-size: 2.8rem;
      font-weight: 900;
      background-color: v.$morado;
      padding: 1rem 0;
      border-radius: 0.8rem;
      cursor: pointer;
      &:hover {
        background-color: color.adjust(v.$morado, $lightness: -20%);
      }
    }
  }
}
